<template>
    <div>
        <div
            class="word"
            v-for="(item, index) in words"
            :key="index"
            @click="paint(item)"
            :class="'color' + item.group"
        >
            <div>
                <a
                    :href="'https://fanyi.baidu.com/#en/zh/' + item.key"
                    target="view_window"
                >
                    {{ item.key }}
                </a>
            </div>
            <div v-show="showChinese">
                {{ item.val }}
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "WordContainer",
    props: ["words", "showChinese", "painterCode"],
    methods: {
        paint(item) {
            if (item.group == this.painterCode) item.group = 0;
            else item.group = this.painterCode;
        },
    },
};
</script>

<style scoped>
.word {
    display: flex;
    width: 380px;
    height: 24px;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    margin: 1px;
    border: solid gray 1px;
}

.color1 {
    background-color:lightpink;
}
.color2 {
    background-color: lightblue;
}
.color3 {
    background-color: lightgreen;
}
</style>