<template>
    <div id="app">
        <WordPainter
            :set_painter_code="set_painter_code"
            :paint_clear="paint_clear"
        />
        <div class="main">
            <p>按z切换显示 方向键翻页 {{ width }}</p>
            <PageBtn :page="page" :pageMax="pageMax" :set_page="set_page" />

            <div class="words">
                <WordContainer
                    :words="words[page][0]"
                    :showChinese="showChinese"
                    :painterCode="painterCode"
                />
                <WordContainer
                    :words="words[page][1]"
                    :showChinese="showChinese"
                    :painterCode="painterCode"
                />
            </div>

            <PageBtn :page="page" :pageMax="pageMax" :set_page="set_page" />
        </div>
    </div>
</template>

<script>
import PageBtn from "@/components/PageBtn";
import WordPainter from "@/components/WordPainter";
import WordContainer from "@/components/WordContainer";
export default {
    name: "App",
    data() {
        return {
            page: 0,
            pageMax: 0,
            words: [],
            showChinese: true,
            width: 0,
            painterCode: 0,
        };
    },
    components: { PageBtn, WordPainter, WordContainer },
    created() {
        var temp = require("./word.json");
        temp.forEach((words_page_t) => {
            var words_page_d = [];
            words_page_t.forEach((words_col_t) => {
                var words_col_d = [];
                Object.keys(words_col_t).forEach((e) => {
                    words_col_d.push({
                        key: e,
                        val: words_col_t[e],
                        group: 0,
                    });
                });
                words_page_d.push(words_col_d);
            });
            this.words.push(words_page_d);
        });
        this.pageMax = this.words.length - 1;
    },
    mounted() {
        document.addEventListener("keyup", this.keyupEvent);
        window.onresize = () => {
            return (() => {
                this.width = document.body.clientWidth;
            })();
        };
        this.width = document.body.clientWidth;
    },
    destroyed() {
        document.removeEventListener("keyup", this.keyupEvent);
    },
    methods: {
        keyupEvent(event) {
            console.log(event);
            if (event.key == "z") {
                this.showChinese = !this.showChinese;
            } else if (event.key == "a" || event.key == "ArrowLeft") {
                this.page = Math.max(0, this.page - 1);
            } else if (event.key == "d" || event.key == "ArrowRight") {
                this.page = Math.min(this.pageMax, this.page + 1);
            }
        },
        set_page(val) {
            this.page = val;
        },
        set_painter_code(val) {
            this.painterCode = val;
        },
        paint_clear() {
            this.words[this.page].forEach((e) => {
                e.forEach((e) => {
                    e.group = 0;
                });
            });
        },
    },
};
</script>

<style>
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 60px;
    display: flex;
    justify-content: center;
}
.main {
    display: grid;
    grid-template-rows: repeat(4, auto);
    width: auto;
    justify-items: center;
    align-items: center;
}

.words {
    display: flex;
    flex-wrap: wrap; /* 方便手机端适配 */
    justify-content: center;
}
</style>
