<template>
    <div class="painter-container">
        <button
            v-for="(item, index) in painters"
            :key="index"
            :class="
                'painter ' +
                item.color +
                (painterCode == item.code ? ' show' : '')
            "
            @click="set_code(item.code)"
        >
            {{ item.name }}
        </button>
        <button @click="paint_clear">清除全部</button>
    </div>
</template>

<script>
export default {
    name: "WordPainter",

    data() {
        return {
            painterCode: 0,
            painters: [
                {
                    color: "red",
                    name: "红",
                    code: 1,
                },
                {
                    color: "blue",
                    name: "蓝",
                    code: 2,
                },
                {
                    color: "green",
                    name: "绿",
                    code: 3,
                },
                {
                    color: "white",
                    name: "空",
                    code: 0,
                },
            ],
        };
    },

    props:['set_painter_code','paint_clear'],

    methods: {
        set_code(val) {
            this.painterCode = val;
            this.set_painter_code(val);
        },
    },
};
</script>

<style scoped>
.painter-container {
    position: fixed;
    right: 10px;
    display: flex;
    flex-direction: column;
    top: 30%;
    gap: 20px;
}

button {
    height: 70px;
    width: 70px;
    font-size: 20px;
    border: solid 1px;
}

.painter {
    opacity: 0.3;
}

.show {
    opacity: 1;
}

.red {
    color: white;
    background-color: red;
}
.blue {
    color: white;
    background-color: blue;
}
.green {
    color: white;
    background-color: green;
}
.white {
    background-color: white;
}
</style>